.tip {
	&-header {
		color: getColor(violet default);
		margin: 0;
		padding-top: 1rem;
		padding-bottom: 0.3rem;
	}
	&-content {
		padding-bottom: 1rem;
		padding-right: 1rem;
		border-top: 4px solid getColor(violet default);
		background-color: getColor(violet lightest);
		padding-left: 1rem;
		margin-left: 0;
	}
}

.gridContainer > .tip {
	margin-top: 1rem;
	margin-bottom: 1rem;
}