html {
	font-size: 100%;

	@include mqGreaterThan(small) {
		font-size: 100% / 16 * 17;
	}

	@include mqGreaterThan(large) {
		font-size: 100% / 16 * 18;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		font-size: 100% / 16 * 20;
	}
}

/* Text-Related Elements */
p {
	margin-bottom: 1em;
}

/* Blockquote (is gelijk aan _tip.scss) */
blockquote {
	font-style: italic;
	border-left: 3px solid getColor(grijs default);
	color: getColor(grijs default);
	padding-left: 1em;
	margin: 1em 0;
}

/* Horizontal Rule */
hr {
	border: 0;
	height: 2px;
	background: getColor(grijs light);
	margin: $doubleSpace 0;
}

abbr {
	border-bottom: 1px dotted getColor(grijs default);
	cursor: help;
}

//Intro text
.intro {
	@include fontSize(introText);
	display: block;
	clear: both;
	color: getColor(hemelblauw darkest);
	$marginMap: multiplyProps(getFontSize(h3), 2);
	@include setPropWithMq(greater, margin-bottom, $marginMap);

	&--alone {
		margin-bottom: 0;
	}

	.is-webfont & {
		font-style: italic;
	}

	.is-fallbackfont & {
		font-style: normal;
	}
}

//Info text
.infoText {
	margin-bottom: 0;
	@include fontSize(small);
	color: getColor(grijs grijs6);
	font-style: italic;

	&--date {
		@include mqGreaterThan(smallMedium) {
			float: right;

			span {
				display: block;
			}
		}
		@include mqGreaterThan(extraLarge) {
			span {
				display: inline;
			}
		}
	}
	&--afzender {
		border-top: 3px solid getColor(grijs border);
		padding-top: 0.75em;
		margin-bottom: 0.75em;
		color: getColor(grijs grijs6);
	}
}

//Source Block
.sourceBlock {

	padding: 1rem 0;

	ul {
		margin-left: 0;
		.verticalItem:first-child {
			margin-left: 0;
		}
		.partnerLogo {
			height: 2.75rem;
		}
	}
}

.blockTitle {
	margin-top: 0;
}

.textLine {
	display: block;
}

.highlight--blue {
	color: getColor(hemelblauw default);
}
.highlight--purple {
	color: getColor(paars default);
}
.highlight--green {
	color: getColor(mosgroen default);
}

.dimmed {
	color: getColor(grijs default);
}


// Only for development purposes!
.yellow-marker {
	background-color: yellow;
}
.yellow-line {
	border-bottom: 10px yellow solid;
}
