.is-menuOpened {
	overflow: hidden;
}

.container {
	width: 100%;
	min-height: 100vh;
}

.menuBar {
	position: relative;
	height: 100%;
	width: $menuBarWidthSmall;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	@include zIndex(menuBar bar);
	padding: {
		top: (2rem/16);
		bottom: (2rem/16);
	}

	@include mqGreaterThan(small) {
		width: $menuBarWidthLarge;
		padding: {
			top: (1rem/17);
			bottom: (1rem/17);
		}
	}

	@include mqGreaterThan(large) {
		width: $menuBarWidthExtraLarge;
		padding: {
			top: (1rem/18);
			bottom: (1rem/18);
		}
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		width: $menuBarWidthExtraExtraLarge;
	}

}

.menuBar--title {
	@extend .h2;
}

.menu-backgroundClose {
	position: fixed;
	display: block;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	z-index: getZIndex(overlay);
	@include cleanButton;
	@include hideText;
	background-color: rgba(getColor(hemelblauw darkest), 0.6);
	width: 100%;
	cursor: default;
}

.is-menuBar-retracted { // todo ongebruikt?
	@include mqLesserThan(small) {
		background: rgba(getColor(wit), 0.9);
		box-shadow: inset -1px 0 0 0 rgba(getColor(grijs light), 1);

		@include before("") {
			width: 0.25rem;
		}
		.menuBar-icon {
			fill: change-color(getColor(hemelblauw light), $lightness: 48%);
		}
	}
}

.menuBar-filler {
	background-color: $menuBarBg;
	flex: 1;
}
