.overviewPageTitle {
	background: getColor(hemelblauw default);
	@include theme((background: foreground), #{&}, true);

	&-text {
		color: getColor(wit);

		margin: {
			top: 0.4rem;
			bottom: 0.3rem;
		}

		@include mqGreaterThan(extraExtraExtraLarge) {
			margin: {
				top: 0.6rem;
				bottom: 0.5rem;
			}
		}
	}
}

.pageTitle {
	display: flex;
	align-items: flex-start;

	&-mobile {
		background: getColor(paars lightest);
		color: getColor(paars default);
		margin: 0;
		padding: {
			left: 1rem;
			right: 1rem;
			bottom: 0.5rem;
		}
		&-title {
			flex-grow: 1;
			padding-top: 0.5rem;
		}
	}
}