// Defaults
$pad : 1rem;
$pad-and-half: $pad * 1.5;
$pad-double: $pad * 2;
$pad-half: $pad / 2;
$pad-quarter: $pad / 4;

// Menu bar
$menuBarWidthSmall: 2rem;
$menuBarWidthLarge: 3.16rem;
$menuBarWidthExtraLarge: 7.9rem;
$menuBarWidthExtraExtraLarge: 10.9rem;

// Menu bar icons
$menuIconSizeRatioSmall: 0.6;
$menuIconSizeRatioLarge: 0.48;
$menuIconSizePaddingRatioSmall: (1 - $menuIconSizeRatioSmall) / 2;
$menuIconSizePaddingRatioLarge: (1 - $menuIconSizeRatioLarge) / 2;

// Max page widths
$maxPageWidth: (
	// px: 1920px,
	em: 120em,
	// px: 1920px,
	rem: 96rem,
);

// Theme variables that can be overwritten for English site
$headerHeadingPaddingTop: 0.9rem !default;
$headerHeadingPaddingBottom: 0.65rem !default;

$rijksLogoBg: getColor(hemelblauw official) !default;
$rijksLogoWeapon: getColor(wit) !default;

$payOffFlex: 0 0 auto !default;
$payOffTextAlign: left !default;
$payOffTextColor: getColor(grijs grijs6) !default;

$menuBarBg: getColor(hemelblauw light) !default;
$menuBarBgStop1: getColor(hemelblauw light) !default;
$menuBarBgStop2: getColor(mintgroen default) !default;
$menuBarOpacity: 0.9 !default;
$menuBarButtonColor: getColor(hemelblauw darkest) !default;
$menuBarButtonHoverColor: getColor(hemelblauw lighter) !default;
$menuBarButtonIsOpenedColor: getColor(hemelblauw lighter) !default;

$brandBarTopBg: getColor(hemelblauw default) !default;

// Content
$storyHeaderTextColor: getColor(wit) !default;
$storyFooterBorderColor: getColor(hemelblauw light) !default;
$themaLinksBgColor: getColor(hemelblauw light) !default;
$themaLinksHoverBgColor: getColor(hemelblauw darker) !default;
$themaLinksHoverTextColor: getColor(wit) !default;
$themaLinksTextColor: getColor(hemelblauw darker) !default;

// Links
$baseLinkColor: inherit !default;
$baseLinkHoverColor: getColor(hemelblauw grijscontrast) !default;

$articleLinkColor: $baseLinkColor !default;
$articleLinkHover: $baseLinkHoverColor !default;