.content-block {
	@extend .block;
	@extend .media;
	@extend .media--alignTop;
	background-color: #e5f0f6;
	padding: 1rem;

	h2, .h2,
	h3, .h3,
	h4, .h4 {
		margin-top: 0;
	}
}

.content-block__icon {
	@extend .media-figure;
	width: 2.5rem;
	fill: getColor(hemelblauw default);
	margin-top: -5px;
}

.content-block__content {
	@extend .media-text;

	.content-block__icon + & {
		padding-left: 1rem;
	}
}