/* HEADER */

// logo styling -> _header-logo.scss
// payOff styling -> _header-payOff.scss

.header {
	background-color: rgba(getColor(wit), 0.9);
	position: relative;
	flex: none;
}

.brandBarTop {
	background: $brandBarTopBg;
	display: flex;
	align-items: center;
	min-height: 1.875rem;
	margin-left: 0;
	padding-left: 0;

	@include mqGreaterThan(small) {
		min-height: 2.94rem;
	}

	@include mqGreaterThan(large) {
		min-height: 3rem;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		// Min-height not used because IE11 will fail to align items center.
		// See http://caniuse.com/#search=align-items, tab 'Known issues'
		height: 3.8rem;
	}
}

.brandBarTop--slim-sticky {
	min-height: 4px;
	position: sticky;
	top: 0;
}

.brandBarBottom {
	background: getColor(hemelblauw lighter);
	min-height: 0.6rem;
	padding-bottom: 6px;
	position: relative;

	@include insideMargin(1.8rem 1rem);

	.labelList-link {
		background-color: getColor(wit);
	}
}

.brandBarPageTitle {
	margin-bottom: 0;
	color: getColor(hemelblauw darkest);
	@include fontSize(brandBarPageTitle);
}

.header-heading {
	padding: {
		top: $headerHeadingPaddingTop;
		bottom: $headerHeadingPaddingBottom;
	}
}