.downloadLink {

	display: inline-flex;
	border-bottom: none;
	margin-bottom: 1rem;

	@include hoverEtc {
		.downloadLink-text {
			background-color: darken(getColor(hemelblauw lighter), 5%);
			transition: background-color 400ms;
		}
		.dl-goIcon {
			margin-left: 10px;
			transition: margin-left 400ms;
		}
	}


	&-dlIcon {
		background: getColor(hemelblauw default);
		border-top-left-radius: 3px;
		border-bottom-left-radius: 3px;
		display: flex;
		align-items: center;
		fill: white;

		svg.dlIcon {
			width: 4rem;
			height: 2.4rem;
		}
	}

	&-titles {
		padding-right: 1rem;
	}

	&-title {
		margin: 0;
	}

	&-subtitle {
		display: flex;
		justify-content: space-between;
	}

	&-text {
		border-top: 3px solid getColor(hemelblauw default);
		border-left: none;
		padding: 0.5rem;
		display: flex;
		align-items: center;
	}

	&-goIcon {
		width: 3rem;
		height: 2rem;
		fill: getColor(hemelblauw darkest);
		svg.dl-goIcon {
			width: 3rem;
			height: 2rem;
		}
	}

}