/* GRID */

$maxWidth: 100rem; //1800px
$space: 2rem;
$doubleSpace: $space * 2; // 4rem
$halfSpace: $space / 2; // 1rem
$quarterSpace: $space / 4; // 0.5rem
$eighthSpace: $space / 8; // 0.25rem


// Returns the value for the with of al column of a certain ratio
// It takes the gutter space into account
// Example:
// calcColumnWidth(1/1) -> calc(100% - 2rem)
// calcColumnWidth(1/2) -> calc(50% - 2rem)
@function calcColumnWidth($ratio) {
	@return calc(#{$ratio * 100%} - #{$space});
}

//for those pesky topbars that are long on the left and short on the right
.shortRight {
	padding-right: 0;
	@include setPropWithMq(greater, margin-right,
		(
			all: 0.2rem,
			extraSmall: 3%,
			small: 1.6rem,
			mediumLarge: 1rem,
			extraLarge: 3.2rem,
			extraExtraExtraLarge: 4.2rem
		)
	);
}

.layoutContainer {
	width: 100%;
	margin-left: auto;
	margin-right: auto;

	border-left: solid transparent;
	max-width: $maxWidth;

	@include setPropWithMq(greater, padding,
		(
			all: 0,
			extraSmall: 0 3% 0 0,
			small: 0 1.6rem,
			mediumLarge: 0 1rem,
			extraLarge: 0 3.2rem,
			extraExtraExtraLarge: 0 4.2rem
		)
	);

	@include setPropWithMq(greater, border-left-width,
		(
			all: $menuBarWidthSmall,
			small: $menuBarWidthLarge,
			extraExtraLarge: $menuBarWidthExtraLarge,
			extraExtraExtraLarge: $menuBarWidthExtraExtraLarge,
		)
	);
}

.layoutContainer--fullWidth {
	max-width: none;
}

// Container for elements that don't use columns
.contentContainer {
	@extend .layoutContainer;
	border-right: $halfSpace solid transparent;

	@include setPropWithMq(greater, border-left-width,
		(
			all: $menuBarWidthSmall + $halfSpace,
			small: $menuBarWidthLarge + $halfSpace,
			extraExtraLarge: $menuBarWidthExtraLarge + $halfSpace,
			extraExtraExtraLarge: $menuBarWidthExtraExtraLarge + $halfSpace,
		)
	);

	&.fullContentWidth {
		padding: 0;
		border-right: 0;

		@include setPropWithMq(greater, border-left-width,
			(
				all: $menuBarWidthSmall,
				small: $menuBarWidthLarge,
				extraExtraLarge: $menuBarWidthExtraLarge,
				extraExtraExtraLarge: $menuBarWidthExtraExtraLarge,
			)
		);
	}
}

.sublayoutContainer {
	margin-left: negative($halfSpace);
	margin-right: negative($halfSpace);
}
.sublayoutContainerSmall {
	margin-left: negative($quarterSpace);
	margin-right: negative($quarterSpace);
}

.gridContainer {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: flex-start;
	align-items: flex-start;
}

.gridContainer--stretchItems {
	align-items: stretch;
}
.gridContainer--alignItemsToEnd {
	align-items: flex-end;
}
.gridContainer--alignItemsCenter {
	align-items: center;
}
.gridContainer--centerItems {
	justify-content: center;
}
.gridContainer--itemsToEnd {
	justify-content: flex-end;
}
.gridContainer--spaceBetweenItems {
	justify-content: space-between;
}
.gridContainer--singleLine {
	flex-wrap: nowrap;
}
.gridContainer--reverse {
	flex-direction: row-reverse;
	justify-content: flex-end;
}
.gridContainer--reducedWidth {
	@include mqGreaterThan(mediumLarge) {
		width: 85%;
	}
	@include mqGreaterThan(extraExtraExtraLarge) {
		width: 75%;
	}
}

.flexIEMinHeightFix {
	display: flex;
	flex-direction: row;
}

.gridItem {
	display: block;
	margin-left: $halfSpace;
	margin-right: $halfSpace;
}

.pad {
	padding-left: $halfSpace;
	padding-right: $halfSpace;
}

.padTop {
	padding-top: $quarterSpace;
}

.padBottom {
	padding-bottom: $quarterSpace;
}

.padVertical {
	padding-top: $quarterSpace;
	padding-bottom: $quarterSpace;
}

.smallMargin {
	margin-top: $halfSpace;
	margin-bottom: $halfSpace;
}

.extraSmallMargin {
	margin-top: $quarterSpace;
	margin-bottom: $quarterSpace;
}

.smallTopMargin {
	margin-top: $halfSpace;
}

.mediumMargin {
	margin-top: $space;
	margin-bottom: $space;
}

.bigMargin {
	margin-top: 3rem;
	margin-bottom: 3rem;
}


.bigBottomMargin {
	margin-bottom: 3rem;
}

.mediumBottomMargin {
	margin-bottom: $space;
}

.smallBottomMargin {
	margin-bottom: $halfSpace;
}

.extraSmallBottomMargin {
	margin-bottom: $quarterSpace;
}

.smallSideMargin {
	margin-left: $quarterSpace;
	margin-right: $quarterSpace;
}

.column {
	@extend .gridItem;

	flex: 0 1 auto; // default
}

.column--fit {
	flex: 1 1 auto;
}

.column--full {
	width: calcColumnWidth(1/1);
}

.column--half {
	@include setPropWithMq(greater, width,
		(
			$oneColumnGridBreakpoint: calcColumnWidth(1/1),
			$twoColumnGridBreakpoint: calcColumnWidth(1/2)
		)
	);
}

.column--quarter {
	@include setPropWithMq(greater, width,
		(
			$oneColumnGridBreakpoint: calcColumnWidth(1/1),
			$twoColumnGridBreakpoint: calcColumnWidth(1/3),
			mediumLarge: calcColumnWidth(1/4)
		)
	);
}

.column--threeQuarter {
	@include setPropWithMq(greater, width,
		(
			$oneColumnGridBreakpoint: calcColumnWidth(1/1),
			$twoColumnGridBreakpoint: calcColumnWidth(2/3),
			mediumLarge: calcColumnWidth(3/4)
		)
	);
}

.column--third {
	@include setPropWithMq(greater, width,
		(
			$oneColumnGridBreakpoint: calcColumnWidth(1/1),
			$twoColumnGridBreakpoint: calcColumnWidth(1/2),
			$threeColumnGridBreakpoint: calcColumnWidth(1/3)
		)
	);
}

.column--fullThird {
	@include setPropWithMq(greater, width,
		(
			$oneColumnGridBreakpoint: calcColumnWidth(1/1),
			$threeColumnGridBreakpoint: calcColumnWidth(1/3)
		)
	);
}

.column--twoThird {
	@include setPropWithMq(greater, width,
		(
			$oneColumnGridBreakpoint: calcColumnWidth(1/1),
			$threeColumnGridBreakpoint: calcColumnWidth(2/3)
		)
	);
}

.sideColumn {
	@include mqLesserThan($threeColumnGridBreakpoint) {
		margin-top: $space;
	}

	@include mqBetween(smallMedium extraExtraLarge) {
		width: 100%;
		margin-left: 0;
		margin-right: 0;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;

		> div, > section, > aside {
			width: calcColumnWidth(1/2);
			display: inline-block;
			margin: 0 1rem;
		}
	}
}

.forceFilledGridLine:nth-child(n + 4) {
	@include mqLesserThan($twoColumnGridBreakpoint) {
		display: none;
	}

	@include mqGreaterThan($threeColumnGridBreakpoint) {
		display: none;
	}
}