/*------------------------------------*\
	$MAIN CONTENT AREA
\*------------------------------------*/
html {
	overflow-y: scroll;
}

$fallbackFonts: Tahoma, sans-serif;

body {
	background: getColor(grijs lightest);
	// added unit size to line-height to keep a consistent vertical rhythm
	-webkit-text-size-adjust: 100%;
	color: getColor(grijs darkest);
}

.is-webfont body {
	font-family: RijksOverheidSans, $fallbackFonts;
	line-height: getLineHeight(root);
}

.is-fallbackfont body {
	font-family: $fallbackFonts;
	font-size: getFallbackFontSize(root);
	line-height: getFallbackLineHeight(root);
}

@mixin focusStyle {
	outline-color: hsl(217, 99%, 65%);
	outline-style: solid;
	outline-width: 2px;
}

*:focus {
	@include focusStyle;
}

.page {
	min-height: 100vh;
	max-width: map-get($maxPageWidth, rem);
	display: flex;
	flex-direction: column;
	transition-property: opacity;
	transition-duration: 400ms;
	background: getColor(wit);

	// To prevent margin overflow;
	padding-top: 1px;
	margin: -1px auto 0 auto;

	.is-menuOpened &:before {
		width: 100%;
		height: 100%;
		display: block;
		position: fixed;
		top: 0;
		left: 0;
		@include zIndex(overlay);
		content: "";
		background-color: getColor("wit");
		opacity: 0.7;
	}

	@include mqGreaterThan(extraExtraExtraLarge) {
		max-width: map-get($maxPageWidth, rem);
	}

}

.is-cardOpened {
	&--small {
		@include mqLesserThan($cardBreakpoint) {
			display: none;
		}
	}
}

.main {
	// Should not change for fallback font
	$marginMap: multiplyProps(getFontSize(h2), 1.5);
	@include setPropWithMq(greater, margin-top, $marginMap);
	@include insideMarginBottom($space);
	flex: 1 0 auto;
	background-color: getColor(wit);

	&--noMargin {
		margin-top: 0;
	}

	&--noMarginBottom {
		&:after {
			display: none;
		}
	}

	&:focus {
		outline: none;
	}
}

.main--flex {
	display: flex;
}

.font-secondary {
	font-family: Tahoma;
}

.hide-svg {
	@include accHide;
}