.feedback-textField {
	margin-top: 1rem;

	.formField-label {
		@include accHide;
	}

	.formField-wrapper {
		display: block;
		width: calcColumnWidth(1/1);
	}

	.formField-optional {
		display: block;
		text-align: right;
	}

	.formField-inputWrapper {
		display: block;
		width: 100%;
	}
}

.feedback-radio > p {
	margin-bottom: 0;
}

.feedback-saved {
	padding-right: 3.5rem;
}

.feedback-saved-title {
	margin-top: 0;
	color: inherit;
}

.feedback-saved-text {
	margin: 0;
}