$awardLogosDisplay: inherit !default;
$footerBg: getColor(hemelblauw lighter) !default;

.footer {
	/*
		Sticky footer is built according to
		https://github.com/philipwalton/solved-by-flexbox/blob/master/assets/css/components/site.css
	*/
	flex: none;
	background: $footerBg;
	@include insideMarginTop(0.5rem);
	@include fontSize(small);

	@include mqGreaterThan(smallMedium) {
		@include fontSize(root);
	}
}

.footerNav-link {
	color: getColor(hemelblauw darkest);

	ul {
		justify-content: center;

		@include mqGreaterThan(extraLarge) {
			justify-content: flex-start;
		}
	}
}

.partnerLogos-intro {
	text-align: center;
}

.partnerLogos {
	margin-bottom: -$pad-half;

	@include mqGreaterThan(large) {
		display: flex;
	}
}

.partnerLogo-list--right {
	@include mqGreaterThan(large) {
		margin-left: auto;
		padding-left: 50px;
	}
}

.partnerLogo--color {
	filter: grayscale(100%);
	&:hover {
		filter: grayscale(0%);
	}
}
.partnerLogo-item {
	position: relative;
	display: inline-block;
	width: auto;
}
.partnerLogo-link {
	display: block;
	border: none;
}
.partnerLogo-image {
	display: block;
	max-width: none;
	height: 2.5rem;
	width: auto;
	transition-property: transform;
	transition-duration: 200ms;
	transition-timing-function: ease-in-out;

	@include mqGreaterThan(large) {
		height: 3rem;
	}

	@include hoverEtc {
		transform: scale(1.1);
	}
}

.partnerLogo-list--awards {
	display: $awardLogosDisplay;
	margin-left: auto;
}

.colophonFrame {
	margin-top: 2rem;
	display: flex;
	align-items: center;
}

.copyright {
	margin-top: 1rem;
}