.articleOverview {
	&--grey {
		.articleOverview-acc-warning {
			color: getColor(grijs grijs6);
			font-style: italic;
		}
	}

	&-acc {
		&-title {
			margin: 0;
			color: getColor(hemelblauw darkest);
			line-height: inherit;
			@include fontSizeAlways(inherit);
			font-weight: bold;
		}

		&-warning {
			color: getColor(hemelblauw default);
		}

		&-list {
			margin-left: 0;
			@include noBullets;
		}

		&-item {
			margin-bottom: 1rem;

			@include hoverEtc {
				.articleOverview-acc-title {
					text-decoration: underline;
				}
			}
		}
	}
}