.story-header {
	padding-top: 2rem;
	padding-bottom: 1rem;
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	background-position: center center;
	background-size: cover;
	color: $storyHeaderTextColor;

	@include before {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: linear-gradient(0deg,rgba(3,48,84,0.95),rgba(3,48,84,0.10));
		z-index: 0;
	}

	@include mqGreaterThan(mediumLarge) {
		height: 75vh;
		padding-top: 0;
		padding-bottom: 5vh;
	}
}

.story-person-info {
	margin-bottom: 0.5rem;
	position: relative;
	z-index: 5;

	.social-link,
	.social-figure {
		width: 1.5rem;
		height: 1.5rem;
	}

	.social-link {
		margin: 0 0.5rem 0 0;
	}

	.social-figure-foreground {
		width: 0.8rem;
		height: 0.8rem;
		top: 0.35rem;
		left: 0.35rem;
	}
}

.story-person-name,
.story-person-age {
	font-weight: bold;

	@include fontSizeWithMq(greater, h3);
	@include lineHeight(textSize);
}

.story-title {
	position: relative;
	z-index: 5;
	color: $storyHeaderTextColor;

	@include before {
		content: "\201C";
		position: relative;
		margin-right: 0.1em;
		@include lineHeight(textSize);
	}

	@include after {
		content: "\201D";
		position: relative;
		margin-left: 0.1em;
		@include lineHeight(textSize);
	}

	@include mqGreaterThan(mediumLarge) {
		padding-left: 1em;

		@include before {
			margin-right: 0;
			position: absolute;
			left: 0;
			@include fontSizeAlways(2em);
		}
		@include after {
			height: 0.5em;
			margin-left: 0;
			padding-left: 0.1em;
			position: absolute;
			bottom: 0.2em;
			right: auto;
			@include fontSizeAlways(2em); //relative to title font size
		}
	}
}

.story-intro {
	position: relative;
	z-index: 5;

	@include mqGreaterThan(mediumLarge) {
		@include fontSize(introText);
	}

	.is-webfont & {
		font-style: italic;
	}

	.is-fallbackfont & {
		font-style: normal;
	}
}

.story-body {
	padding-top: 3rem;

	h2:first-child,
	h3:first-child {
		margin-top: 0;
	}
}

.story-footer {
	margin-top: 1rem;
	margin-bottom: 2rem;
	border-top: 1px solid $storyFooterBorderColor;

	h2:first-child,
	h3:first-child {
		margin-top: 1.5rem;
	}
}

.story-related-quote {
	margin-top: 0.75rem;

	@include before {
		content: "\201C"; // Open quote
	}

	@include after {
		content: "\201D"; // Sluit quote
	}
}

.story-related-link {
	border-bottom: 0;
}