.webinar {

	&-showcase {

		&-header {
			background-color: getColor(hemelblauw lighter);

			&-title {
				padding-top: 2rem;
				padding-bottom: 2rem;
				margin-bottom: 0;
				color: getColor(hemelblauw darkest);
			}
		}

		&-title {
			color: inherit;
		}

		&-image {
			width: 100%;
			min-height: 17rem;
			position: relative;
			object-fit: cover;
		}

		&-label {
			@include cleanButton;
			color: getColor(wit);
			padding: 0.5rem 1rem;
			@include theme((background-color: foreground), ".webinar");
			text-align: center;
			width: 6rem;
		}

		&-featured {

			&-video {
				position: relative;
			}

			&-meta {
				display: flex;
				flex-direction: column;

				.interfaceLink {
					@include theme((color: foreground, fill: foreground), ".webinar");
					font-weight: bold;
				}

				&:hover {
					@include theme((color: foreground), ".webinar");
				}
			}
		}
	}

	&-detail {
		&-type {
			font-weight: normal;
		}

		&-content {
			margin-top: 2rem;
		}
	}

	&-links {
		.linkList {
			display: flex;
			flex-direction: row;

			.linkList-item {
				flex: 1 0 auto;
			}
		}
	}
}

.playButton {
	@include centerAbsolute(5rem, 5rem);
	.promoBlock & {
		@include centerAbsolute(3rem, 3rem);
	}

	@include theme((fill: foreground), ".promoBlock");
	@include theme((fill: foreground), ".webinar");

	.playButtonBg {
		opacity: 0.75;

		.webinar:hover & {
			opacity: 0.9;
		}

		.webinar-showcase-featured-video:hover & {
			opacity: 0.9;
		}
	}

	.webinar:hover &, .webinar-showcase-featured-video:hover & {

		transform: scale(1.02);

	}
}

.embed-container iframe {
	width: 100%;
	height: 21rem;
}
