.tabButton {
	@include cleanButton;
	padding: {
		left: 1rem;
		right: 1rem;
		top: 0.5rem;
		bottom: 0.5rem;
	}
	font-weight: bold;
	margin-right: 1rem;
	color: black;
	fill: getColor(hemelblauw default);

	&-icon {
		width: 1.5rem;
		height: 1.5rem;
		top: 0;
		transition: transform 200ms;
	}

	@include hoverEtc {
		.tabButton-icon {
			position: relative;
			transform: translateY(2px);
		}
	}

	&--opened {
		background: getColor(wit);

		@include hoverEtc {
			.tabButton-icon {
				transform: translateY(-2px);
			}
		}
	}

}