$footerLinkTextColor: getColor(zwart) !default;
$footerLinkFillColor: $footerLinkTextColor !default;
$footerLinkHoverTextColor: getColor(zwart) !default;
$footerLinkHoverFillColor: $footerLinkHoverTextColor !default;

// Footer Nav
.footerNav {
	margin: {
		top: 0.5rem;
		bottom: 0.5rem;
	}

	&-link {
		@include noUnderline;
		color: $footerLinkTextColor;
		fill: $footerLinkFillColor;

		@include hoverEtc {
			@include underline;
			color: $footerLinkHoverTextColor;
			fill: $footerLinkHoverFillColor;
		}
	}
}