.menuBar-context {
	margin-top: auto;
	transform: translateY($menuBarWidthSmall);
	transition: transform 0.2s;

	&:before {
		width: 100%;
		height: $menuBarWidthSmall + 0.3rem;
		content: "";
		position: absolute;
		bottom: 98%;
		left: 0;
		background-color: $menuBarBg;
	}

	@include mqGreaterThan(small) {
		transform: translateY($menuBarWidthLarge);

		&:before {
			height: $menuBarWidthLarge + 0.3rem;
		}
	}
}

.menuBar-context--showBackToTop {
	transform: none;
}